  .nav-item .item{
    position: relative;
    display: block;
    text-decoration: none;
    color: #ecf0f1;
    transition: 0.5s;
    margin-right: 10px;
  }
  .nav-item .item::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 3px;
    top: 100%;
    left: 0;
    background: #EB5757;
    transition: transform 0.5s;
    transform: scaleX(0);
    transform-origin: right;
  }
  .nav-item .item:hover {
    color: #95a5a6;
  }
  .nav-item .item:hover::after {
    transform: scaleX(1);
    transform-origin: left;
  }